<template>
  <titlepanel title="  ">
    <div class="white">
      <div class="subTitle">
        <b></b>
      </div>
      <v-row>
        <!-- <v-col
          cols="12"
          md="3">
          <v-autocomplete
            v-model="searchProduct"
            :items="productNames"
            label=""
            dense
            outlined
            solo/>
        </v-col> -->
      
        <v-col
          cols="12"
          md="3">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            transition="scale-transition"
            offset-y
            min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="dateText"
                label=""
                prepend-icon="mdi-calendar"
                readonly
                dense
                outlined
                flat
                solo
                v-bind="attrs"
                v-on="on">
              </v-text-field>
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range>
              <v-spacer></v-spacer>
              <v-btn
                text
                color="primary"
                @click="menu = false">
                Cancel
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates); dateRangeText();">
                OK
              </v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>

        <v-col
          cols="12"
          md="4"
          class="d-flex flex-row">
          <v-btn @click="changeDate('today')"></v-btn>
          <v-btn @click="changeDate('week')"></v-btn>
          <v-btn @click="changeDate('month')"></v-btn>
          <v-btn @click="changeDate('year')"></v-btn>
        </v-col>

        <!-- <v-col
          cols="12"
          md="1"
          class="pt-3">
          <v-btn
            class="d-flex"
            :disabled = "searchProduct == '' || dates == null"
            @click="calculateSum">
            
          </v-btn>
        </v-col> -->

       
      </v-row>
    </div>      

    <v-card>
    <v-card-title>
      
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label=""
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="products"
      :items-per-page="10"
      class="elevation-1"
      item-key="name"
      :search="search"
      :custom-filter="filterOnlyCapsText">

      <template v-slot:footer>
        <v-toolbar flat>
          <v-spacer></v-spacer>

          <v-dialog
            v-model="dialog"
            max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on">
                
              </v-btn>
            </template>

            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row v-if="editedIndex!=-1">
                    <v-text-field
                      v-model="editedItem.name"
                      label=""
                      readonly>
                    </v-text-field>
                  </v-row>
                  <v-row v-else>
                    <v-text-field
                      v-model="editedItem.name"
                      label=""
                      :rules="[required]">
                    </v-text-field>
                  </v-row>
                  <v-row>
                    <v-text-field
                      v-model="editedItem.stock"
                      label="">
                    </v-text-field>
                  </v-row>
                  <v-row>
                    <v-text-field
                      v-model="editedItem.price"
                      label=""
                      :rules="[required]">
                    </v-text-field>
                  </v-row>
                </v-container>
                <v-container v-if="error" class="danger-alert">
                  {{error}}
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close">
                  
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                  :disabled="editedItem.name=='' || editedItem.price==''">
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"></v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogSearch" max-width="800px">
            <v-card>
              <v-card-title class="text-h5 justify-space-between">
                <div>
                  {{editedItem.name}}
                </div>
                <div class="text1 d-flex flex-row px-2">{{sum}}</div>

              </v-card-title>
              <v-card-text>
                <v-data-table
                  :headers="tradeHeaders"
                  :items="trades"
                  item-key="id"
                  class="elevation-1">
                  <template
                    v-for="header in tradeHeaders.filter((header) => header.hasOwnProperty('formatter'))"
                    v-slot:[`item.${header.value}`]="{ header, value }">
                    <!-- {{ header.formatter(value) }} -->
                    <div
                      v-if="header.value == 'grandPrice' && value>0" 
                      class="blue-text">
                      {{ header.formatter(value) }}
                    </div>
                    <div 
                      v-else-if="header.value == 'grandPrice' && value<0"
                      class="red-text">
                        {{ header.formatter(value) }}
                    </div>
                    <div 
                      v-else-if="header.value == 'grandPrice' && value==0"
                      class="zero">
                        {{ header.formatter(value) }}
                    </div>
                    <div 
                      v-else>
                      {{ header.formatter(value) }}
                    </div>
                  </template>
                  
                </v-data-table>
              </v-card-text>
            </v-card>
          </v-dialog>

        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)">
          mdi-pencil
        </v-icon>
        <v-icon
          small
          class="mr-2"
          @click="deleteItem(item)">
          mdi-delete
        </v-icon>
        <v-icon
          small
          class="mr-2"
          @click="searchItem(item)">
          mdi-card-search
        </v-icon>
      </template>

      <template v-slot:no-data>
        <p class="ma-3"></p>
        <p class="ma-3"></p>
        <v-btn
          class="mb-3"
          color="primary"
          @click="initialize">
          
        </v-btn>
      </template>

    </v-data-table>
    </v-card>

  </titlepanel>
</template>

<script>
import Titlepanel from '../TitlePanel.vue'
import ProductService from '@/services/inwards/ProductService'
import OtherTradeService from '@/services/inwards/OtherTradeService'

export default {
  components: { Titlepanel },
  data: () => ({
    tradeHeaders: [
      { text: '', value: 'made_date', sortable: false, width: '105px',
        formatter: (x) => x.slice(0,10)},
      { text: '', align: 'start', sortable: false, value: 'customer_name'},
      { text: '', value: 'uintPrice', sortable: false,
        formatter: (x) => (""+x).replace(/\B(?=(\d{3})+(?!\d))/g, ",")},
      { text: '', value: 'number', sortable: false,
        formatter: (x) => (""+x).replace(/\B(?=(\d{3})+(?!\d))/g, ",")},
      { text: '', value: 'aTradePrice', sortable: false,
        formatter: (x) => (""+x).replace(/\B(?=(\d{3})+(?!\d))/g, ",")},
      { text: '', value: 'grandPrice', sortable: false,
        formatter: (x) => (""+x).replace(/\B(?=(\d{3})+(?!\d))/g, ",")},
      { text: '', value: 'payLoanDate', sortable: false, width: '105px',
        formatter: (x) => x? x.slice(0,10):'-'},
      { text: '', value: 'comment', sortable: false},
    ],
    trades:[],

    search:'',
    sum: 0,
    today: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateText: '',
    dates: [],
    menu: false,
    
    // searchProduct:'',
    error:'',
    required: (value) => !!value || 'Required.',
    dialog: false,
    dialogDelete: false,
    dialogSearch: false,
    headers: [
      {
        text: '',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      { text: '', value: 'stock', sortable: false},
      { text: '', value: 'price', sortable: false},
      { text: '', value: 'actions', sortable: false }
    ],
    products: [],
    productNames: [],
    editedIndex: -1,
    editedItem: {
      name: '',
      stock: '',
      price: ''
    },
    defaultItem: {
      name: '',
      stock: '',
      price: ''
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? '' : ''
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    dialogSearch(val) {
      val || this.closeSearch()
    },
  },

  created () {
    this.$loading(true)
    this.initialize()
    this.$loading(false)
  },

  methods: {

    changeDate (x){
      var date;
      if(x=='today'){
        this.dates[0] = this.today
        this.dates[1] = this.today
      }else if(x=='week'){
        date = new Date(this.dates[0])
        date.setDate(date.getDate() - 7);
        this.dates[0] = date.toISOString().substr(0, 10)
      }else if(x=='month'){
        date = new Date(this.dates[0])
        date.setMonth(date.getMonth() - 1);
        this.dates[0] = date.toISOString().substr(0, 10)
      }else if(x=='year'){
        date = new Date(this.dates[0])
        date.setFullYear(date.getFullYear()-1);
        this.dates[0] = date.toISOString().substr(0, 10)
      }
      this.dateRangeText()
    },


    filterOnlyCapsText (value, search, item) {
      return value != null &&
            search != null &&
            typeof value === 'string' &&
            value.toString().indexOf(search) !== -1
    },
    
    // async calculateSum(){
    //   this.$loading(true)
    //   this.sum = 0
    //   var startDate = new Date(`${this.dates[0]}T00:00:00`)
    //   var endDate = new Date(`${this.dates[1]}T24:00:00`)
    //   // console.log(startDate.toISOString()+','+endDate.toISOString())
    //   var allItems = (await OtherTradeService.getAllItemsByProduct(this.searchProduct, startDate.toISOString()+','+endDate.toISOString())).data
    //   console.log(allItems)
      
    //   allItems.forEach(item=>{
    //     this.sum+=item.number
    //   })
    //   this.$loading(false)
    // },

    dateRangeText () {
      if(this.dates[1] == undefined){
        this.dates[1] = this.dates[0]
      }
      if(new Date(this.dates[0])> new Date(this.dates[1])){
        var tempDate = this.dates[1]
        this.dates[1] = this.dates[0]
        this.dates[0] = tempDate
      }
      this.dateText = this.dates[0] + '~' + this.dates[1]
    },

    checkInp(x){
      if (isNaN(x)){
        this.error="";
        return true;
      }
      return false;
    },

    async initialize () {
      this.products = (await ProductService.index()).data
      this.productNames = this.products.map(value => value.name)

      // get dates range
      var date = new Date(this.today)
      date.setDate(date.getDate() - 30);
      this.dates[0] = date.toISOString().substr(0, 10)
      this.dates[1] = this.today
      this.dateRangeText()
    },

    editItem (item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    async searchItem (item) {
      this.sum = 0

      var startDate = new Date(`${this.dates[0]}T00:00:00`)
      var endDate = new Date(`${this.dates[1]}T24:00:00`)
      this.trades = (await OtherTradeService.getAllItemsByProduct(item.name, startDate.toISOString()+','+endDate.toISOString())).data
      console.log(this.trades)

      // the amounts
      this.trades.forEach(item=>{
        this.sum+=item.number
      })

      this.editedItem = Object.assign({}, item)

      this.dialogSearch = true
    }, 

    async deleteItemConfirm () {
      this.$loading(true)
      try{
        await ProductService.delete(this.editedItem)
        this.closeDelete()
      }catch(err){
        console.log(err)
      }
      this.initialize()
      this.$loading(false)
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.error=""
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeSearch () {
      this.dialogSearch = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async save () {
      this.$loading(true)
      var item = this.editedItem

      // check input is correct
      if(this.checkInp(item.price) || this.checkInp(item.stock)) {
        this.$loading(false)
        return
      }
      if(item.stock == '') item.stock = 0

      if (this.editedIndex > -1) {
        try{
          await ProductService.put(item)
          this.initialize()
        }catch(err){
          console.log(err)
        }
      } else {
        const checkSameItem = (item) => item == this.editedItem.name;
        if(this.productNames.some(checkSameItem)) {
          this.error=""
          this.$loading(false)
          return
        }
        try{
          await ProductService.post(item)
          this.initialize()
        }catch(err){
          console.log(err)
        }
      }
      this.close()
      this.$loading(false)
    },
  },
}
</script>
<style scoped>
.text1{
  height: auto;
  text-align: left;
  font-size: 24px;
  padding: 5px 10px;
  color: white;
  background-color: #1976D2;
  border-radius: 10px;
}
</style>

